import React, { useEffect, useState, useContext} from 'react'
import './style.scss'
import Context from 'Contexts/global'
import { Search } from 'Components'
import $ from 'jquery'
const News = ( ) => {
	const state = useContext(Context)
	const [ data, setData] = useState(false)
	const [ searchTerm, setSearchTerm] = useState('')
	const [selectedArticle, setSelectedArticle] = useState(false)
	useEffect(() => {
		console.log('getting news')
		$.get(`${state.host}/news` , (res) => {
			setData(res.articles)
		})
		return () => {

		}
	}, [])

	function onSubmitSearch(){
		$.get(`${state.host}/news?s=${searchTerm}` , (res) => {
			setData(res.articles)
		})
	}


	function getDate(date){
		let d = new Date(date)
		return d.toUTCString();
	}
	return(
		<div className={`newsContainer ${selectedArticle && 'isSelected'}`}>
			<Search onSubmit={onSubmitSearch} placeholder="Search keyword" onChange={(e) => setSearchTerm(e.target.value)} value={searchTerm} />
			<div className="newsInnerContainer">
			{data && data.map((item, i ) => {
				return(
					<div className="newsArticle" onClick={() => {
						console.log('selecting item', item)
						setSelectedArticle(item)}
					}>
						<div className="newsArticleInner">
						<div className="imageOuterContainer">
							<div className="image" style={{backgroundImage: `url(${item.urlToImage})`}} />
						</div>
						<p className="title">{item.title}</p>
						
						<p className="description">{item.description}</p>

						<p className="source">{item.source.name}</p>
						<p className="publishedDate">{getDate(item.publishedAt)}</p>
						</div>
					</div>
				)
			})}
		</div>
		{selectedArticle && 
			<div className="selectedArticle">
				<div className="newsArticle" onClick={() => setSelectedArticle(false)}>
							<div className="newsArticleInner">
							<div className="imageOuterContainer">
								<div className="image" style={{backgroundImage: `url(${selectedArticle.urlToImage})`}} />
							</div>
							<p className="title">{selectedArticle.title}</p>
							
							<p className="description">{selectedArticle.description}</p>
							<p className="description">{selectedArticle.content}</p>
							<p className="source">{selectedArticle.source.name}</p>
							<p className="publishedDate">{getDate(selectedArticle.publishedAt)}</p>
					</div>
				</div>
				</div>

		}
		</div>
	)
}

export default News