import React from 'react'
import './style.scss'

const Member = ({nameDisplayAs,thumbnailUrl, supporter, latestParty}) => {
	return(
		<div className="memberContainer">	
			<div className="image" style={{backgroundImage:`url(${thumbnailUrl})`}} />
			<p>{nameDisplayAs}</p>
			<p>{latestParty.name}</p>
			<p>{(supporter === true || supporter === false) ? supporter ? 'Voted for' : 'Voted against' : ''}</p>
		</div>
	)
}

export default Member