import React, { useState, useContext} from 'react'
import { navigate } from 'hookrouter'
import './style.scss'
import MenuItems from 'Data/menu'
import Context from 'Contexts/global'

const Menu = ( ) => {
	const state = useContext(Context)
	const [path, setPath ] = useState(window.location.pathname)

	return(
		<React.Fragment>
		<div className="MenuPlaceholder" />
		<div className="MenuContainer">
			{MenuItems && MenuItems.map((item, i) => {
				return(
					<div 
						key={i}
						className={`MenuItemContainer ${item.route === path && 'isSelected'}`} 
						onClick={() => {
							setPath(item.route)
							navigate(item.route)}
						}>
						<div className="imageContainer">
							<img src={item.icon} />
							<p>{item.label}</p>
						</div>	
						
						{item.submenu && 
							<div className="subMenuOuterContainer">
							<div className="submenu">
								{item.submenu.map((subitem, j) => {
									return(
										<div 
										key={`${i}${j}`}
										className={`SubMenuItemContainer ${subitem.route === path && 'isSelected'}`} 
										onClick={(e) => {
											e.stopPropagation()
											setPath(subitem.route)
											navigate(subitem.route)}
										}>
										<div className="subimageContainer">
											<img src={subitem.icon} />
											<p>{subitem.label}</p>
										</div>	
										</div>
									)
								})}
							</div>
							</div>
						}
					</div>
				)
			})}
			<div className="PostController" onClick={() => state.setShowArticle(!state.showArticle)}>
							<img key={state.showArticle}src={state.showArticle ? require('Assets/Icons/minus.svg') : require('Assets/Icons/add.svg')} />
						</div>
		</div>
		</React.Fragment>
	)
}

export default Menu