import React, { useEffect, useState } from 'react'

import { returnJSON } from 'Helpers/general'
import { EditModal , Button, SectionTitle, ChooseParty, Draggable, ChooseDepartment, RowItem, Rail, Tweet } from 'Components'
import { useQuery, useMutation } from '@apollo/react-hooks'
import { navigate  } from 'hookrouter'
import gql from 'graphql-tag'
import './style.scss'

const GET_PERSON = gql`
  query Person($personId: ID!) {
    Person(personId: $personId) {
      personId
      name
      party{
        name
        partyId
      }
      twitter
      departments{
        name
        departmentId
      }
      image

    }
  }
`;

const REMOVE_PERSON_PARTY  = gql`
  mutation RemovePersonParty($personId: ID!, $partyId:ID!) {
    RemovePersonParty(from:{personId:$personId}, to:{partyId: $partyId} ){
      from{
        personId
      }
      to{
        partyId
      }

    }
  }
`;
const REMOVE_PERSON_DEPARTMENT  = gql`
  mutation RemovePersonDepartments($personId: ID!, $departmentId:ID!) {
    RemovePersonDepartments(from:{personId:$personId}, to:{departmentId: $departmentId} ){
      from{
        personId
      }
      to{
        departmentId
      }

    }
  }
`;


export default function Person(props) {
  const [ editing, setEditing ] = useState(false)
  const [removePersonParty] = useMutation(REMOVE_PERSON_PARTY);
  const [removePersonDepartment] = useMutation(REMOVE_PERSON_DEPARTMENT);
  const [ isAddingDepartment, setIsAddingDepartment] = useState(false)
  const { loading, error, data, refetch } = useQuery(GET_PERSON, {
    variables: { personId: props.id},
  })


  function onRemoveParty(party, person){
    removePersonParty({ variables: {partyId: party, personId: person} }).then(() => {
      refetch()
      // callback()
    })
  }
  function onRemoveDepartment(department, person){
    console.log('removing', department, person)
    removePersonDepartment({ variables: {departmentId: department, personId: person} })
    .then(() => {
      
      refetch()
      
      // callback()
    })
  }

  if (error) return <p>Error</p>
  if (loading) return <p>Loading</p>
  if (data && data.Person && data.Person[0]){
    let { name, departments, party, personId } = data.Person[0]
  return (
    <div className="PersonContainer">
      <SectionTitle title={"Party"} secondary/>
      {party && 
        <RowItem >
        <div onClick={() => navigate(`/party/${party.partyId}`)}>
        <p>{party.name}</p>
        </div>
        <div className="remove" onClick={() => onRemoveParty(party.partyId, personId)}>
          <img src={require('Assets/Icons/trash.svg')} />
        </div>
        </RowItem>
      }
      {!party &&
        <ChooseParty onComplete={() => refetch()} personId={personId}/>
      }
      <SectionTitle title={"Departments"} secondary/>
      {departments && departments.map((department, i) => {
        return(
          <RowItem key={i}>
            <div onClick={() => navigate(`/department/${department.departmentId}`)}>
            <p>{department.name}</p>
            </div>
            <div className="remove" onClick={() => onRemoveDepartment(department.departmentId, personId)}>
              <img src={require('Assets/Icons/trash.svg')} />
            </div>
          </RowItem>
        )
      })
        
      }
      
        <ChooseDepartment title="Add department" onComplete={() => refetch()} personId={personId}/>
      
     
    </div>
  )
  } else {
    console.log('no data', data)
    return <div />
  }
}
