

import React, { useEffect, useState } from 'react'
import Edit from './edit'
import { navigate } from 'hookrouter'
import { EditModal , Button, RowItem, SectionTitle, TextInput } from 'Components'
import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import './style.scss'

const GET_PEOPLE = gql`
{
  Person{
    personId
    name
    jobTitle
    image
    twitter
    
  }
}`


export default function Departments() {
  const [ editing, setEditing ] = useState(false)
  const { loading, error, data, refetch } = useQuery(GET_PEOPLE)
  const [people, setPeople ] = useState([])
  const [searchInput, setSearchInput] = useState('')
  function onCancelModal(){
    setEditing(false)
    refetch().then((data) => {
      
    })    
  }

  function onSearch(e){
    setSearchInput(e.target.value)
    if (e.target.value.length < 1) return setPeople(data.Person)
    // console.log(people)
    let newPeople = Object.assign([],data.Person)
    setPeople(newPeople.filter(p => p.name.toLowerCase().indexOf(e.target.value.toLowerCase()) > -1))
    
  }

  useEffect(() => {
    if (data && data.Person){
      setPeople(data.Person)
    }
    return () => {

    }
  },[data])

  if (error) {
  	
  	return <p>Error</p>
  }
  if (loading) return <p>Loading</p>

  return (
    <div className="DepartmentsContainer">
      <SectionTitle title="People" onAdd={() => setEditing('add')}/>
      <TextInput placeholder="Search people"onChange={onSearch} value={searchInput } />
      {people && people.map((item, i) => {
        return(
          <RowItem key={i} onEdit={() => setEditing(item)} onClick={() => navigate(`person/${item.personId}`)}>
            <div className="row">
            <div className="logo cover" style={item.image ? {backgroundImage:`url(${item.image})`} : {border:'1px solid black'}} />
            <div className="column">
            <p>{item.name}</p>
           <p className="subText">{item.jobTitle}</p>
            </div>
            
            </div>
          </RowItem>
        )
      })}
      {editing &&
        <EditModal >
          <Edit {...editing} isAdd={editing === 'add'} onCancel={onCancelModal} onAddRelationships={(persons) => setEditing(persons)}/>
        </EditModal>
      }
    </div>
  )
}

