import React, { useContext, useEffect, useState } from 'react'
import { Vote, Search, SectionTitle } from 'Components'
import Context from 'Contexts/global'
import $ from 'jquery'

const Parliament = (props) => {
	const state = useContext(Context)
	const [ votes, setVotes ] = useState(false)
	const [ vote, setVote] = useState(false)
	useEffect(() => {
		$.get(`${state.host}/divisions` , (res) => {
			console.log('got divisions', res)

			setVotes(res.reverse())
		})
		
		return() => {

		}
	}, [])

	function onVoteClick(index, id){
		let newVote = Object.assign({}, votes[index])
		$.get(`${state.host}/division?id=${id}` , (res) => {
			console.log('got vote ')
			newVote.AyeMembers = res.Ayes 
			newVote.NoeMembers = res.Noes 
			setVote(newVote)
		})
	}

	return (
		<div className="ParliamentContainer">
			<SectionTitle title="Recent votes" />

			{!vote && votes && votes.map((voteID, i) => {
				return(
					<Vote item={voteID} index={i} key={i} {...voteID} onClick={onVoteClick}/>
				)
			})}
			{vote &&
				<div>
				
				<Vote draggableMembers item={vote} {...vote} onClose={() => setVote(false)}/>
				</div>
			}
		</div>
	)
    
}

export default Parliament