import React, { useState, useContext, useEffect} from 'react'
import { TextInput } from 'Components'
import { useQuery } from '@apollo/react-hooks'
import'./style.scss';



const Tagger = ({title, createTag, selected, setSelected, GET_QUERY}) => {
	const { loading, error, data, refetch } = useQuery(GET_QUERY)
	const [ isLoading, setIsLoading] = useState(false)
	const [idKey, setIdKey] = useState(false)

	const [search, setSearch] = useState('')
	const [ tags, setTags] = useState([])
	
	let unselected = tags.filter(t => selected.indexOf(t[idKey])< 0 && t.name.toLowerCase().indexOf(search.toLowerCase()) > -1)
	

	function selectTag(tag){
		let newSelected=Object.assign([], selected)
		
			// newSelected.push(unselected[index])	
		
		newSelected.push(tag)
		setSelected(newSelected)
		// let name = unselected[index].name 
		// let tagIndex = tags.findIndex((t) => t.name === name)
		// let newTags = Object.assign([], tags)
		// newTags[tagIndex].selected = true
		// setTags(newTags)
	}

	function deselectTag(index){
		let newSelected=Object.assign([], selected)
		newSelected.splice(index, 1)
		setSelected(newSelected)
	}

	function internalCreateTag(){
		setIsLoading(true)
		createTag({ variables: {name: search}})
		.then((tag) => {
			refetch()
		})
		.catch((err) => {
			console.log('error in create')
		})
		// let newTag = {name: search}
		// let newTags = Object.assign([], tags)
		// newTags.push(newTag)
		// setTags(newTags)


	}

	function findIdKey(item){
		if (item){
			let keys = Object.keys(item)
			let key = keys.find(k => k.slice(-2) === 'Id')
			setIdKey(key)
		} else {
			console.log('NO ITEM')
		}
		
	}

	useEffect(() => {
		if (data){
			let key = Object.keys(data)
			setTags(data[key])
			findIdKey(data[key][0])
			
			if (search.length > 0){
				let newTag = data[key].find(t => t.name === search)
				
				selectTag(newTag[idKey])
				setIsLoading(false)
				setSearch('')
			}
			
		}
	},[data])
	
	return(
		<div className="tagOuterContainer">
		<p className="title">{title}</p>
		<div className="tagContainer">
		<TextInput style={{border:'none', margin:0}} hideLabel placeholder={createTag ? 'Search or create new tag' : "Search"}onChange={(e) => setSearch(e.target.value)} value={search}/>
		<div className="bottomContainer">
			{!loading && !isLoading && !error &&
			<React.Fragment>
				
				
					{selected.length > 0&&
					<div className="selectedContainer">
					{selected.map((id, i) => {
						let tag = tags.find(t => t[idKey] === id)
						return(
							<div className="selectedTagOuterContainer" key={i} onClick={(e) => {
								e.stopPropagation()
								deselectTag(i)}
							}>
								<p>{tag.name}</p>
								<img src={require('Assets/Icons/closeBlack.svg')} />
							</div>
						)
					})}
					</div>
					}
					<div className="unselectedContainer">
					{unselected.map((tag, i) => {
						
						return(
							<div className="unselectedTagOuterContainer" key={i} onClick={(e) => {
								e.stopPropagation()
								selectTag(tag[idKey])
							}}>
								<p>{tag.name}</p>
							</div>
						)
					})}
					{unselected.length === 0 && search.length > 0 && createTag &&
						<React.Fragment>
							<p className="title noTagsFound">No tags found</p>
							<div className="unselectedTagOuterContainer"onClick={(e) => {
								e.stopPropagation()
								internalCreateTag()
							}}>
								<p>{`Create tag "${search}"`}</p>
							</div>
						</React.Fragment>
					}
					</div>
				
			</React.Fragment>
			}
			</div>
		</div>
		</div>
	)
}


export default Tagger