import React from 'react'
import {useRoutes} from 'hookrouter';
import { Home, Postcode, Person, People, NotFound, Posts, Party, Parties, News, Parliament, Members, Social, Images, Departments, Department } from 'Containers'
import './App.scss'
const routes = {
    '/': () => <Social />,
    '/departments': () => <Departments />,
    '/department/:id': ({id}) => <Department id={id}/>,
    '/parties': () => <Parties />,
    '/person/:id': ({id}) => <Person id={id}/>,
    '/people': () => <People />,
    '/party/:id': ({id}) => <Party id={id}/>,
    '/postcode': () => <Postcode />,
    '/members': () => <Members />,
    '/govt': () => <Parliament />,
    '/news': () => <News />,
    '/images': () => <Images />,
    '/posts': () => <Posts />,

};
  
const MyApp = () => {
    const routeResult = useRoutes(routes);
    return routeResult || <NotFound />;
}

export default MyApp