import React, { useEffect, useState } from 'react'
import Edit from './edit'
import { navigate } from 'hookrouter'
import { EditModal , RowItem, Button, SectionTitle } from 'Components'
import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import './style.scss'

const GET_DEPARTMENTS = gql`
{
  Department {
    name
    departmentId
  }
}`


export default function Departments() {
  const [ editing, setEditing ] = useState(false)
  const { loading, error, data, refetch } = useQuery(GET_DEPARTMENTS)

  function onCancelModal(){
    setEditing(false)
    refetch().then((data) => {
      
    })    
  }

  if (error) return <p>Error</p>
  if (loading) return <p>Loading</p>

  return (
    <div className="DepartmentsContainer">

      <SectionTitle title="Government departments" onAdd={() => setEditing(true)}/>
      {data && data.Department && data.Department.map((item, i) => {
        return(
          <RowItem onEdit={() => setEditing(item)} onClick={() => navigate(`department/${item.departmentId}`)}>
            <p className="departmentTitle">{item.name}</p>     
          </RowItem>
        )
      })}
      {editing &&
        <EditModal >
          <Edit {...editing} onCancel={onCancelModal}/>
        </EditModal>
      }
    </div>
  )
}
