import React, { useEffect, useState } from 'react'

import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import govDepartments from 'Data/govDepartments'
import { TextInput, Button } from 'Components'

const ADD_DEPARTMENT = gql`
  mutation CreateDepartment($name: String!) {
    CreateDepartment(name: $name) {
      name
    }
  }
`;

const UPDATE_DEPARTMENT = gql`
  mutation UpdateDepartment($departmentId: ID!, $name: String!) {
    UpdateDepartment(departmentId: $departmentId, name: $name) {
      departmentId
      name
    }
  }
`;
const DELETE_DEPARTMENT = gql`
  mutation DeleteDepartment($departmentId: ID!) {
    DeleteDepartment(departmentId: $departmentId) {
      departmentId
    }
  }
`;

export default function CreateDepartment(props) {
  const [addDepartment] = useMutation(ADD_DEPARTMENT);
  const [updateDepartment] = useMutation(UPDATE_DEPARTMENT);
  const [deleteDepartment] = useMutation(DELETE_DEPARTMENT);
  const [name, setName] = useState(props.name || '')

  function submit(){
    console.log(props)
    if (!props.departmentId){
      addDepartment({ variables: { name: name} })
      .then(() => {
        props.onCancel()
      })
      .catch((err) => props.onCancel());
      
    } else {
      updateDepartment({ variables: {departmentId: props.departmentId, name: name} })
      .then(() => {
        props.onCancel()
      })
      .catch((err) => props.onCancel());
      
    }
  }

  function onDelete(){
    deleteDepartment({ variables: {departmentId: props.departmentId} })
    .then(() => {
      props.onCancel()
    })
    .catch((err) => props.onCancel());
  }

  return (
    <React.Fragment>    
      <TextInput onChange={(e) => setName(e.target.value)} value={name} placeholder="Name" />
      <div className="actionContainer">
          <Button style={{marginLeft:'4px'}}label="Save" onClick={submit}/>
          <Button style={{marginLeft:'4px'}}label="Cancel" secondary onClick={props.onCancel}/>
          <Button style={{marginLeft:'4px'}}label="Delete" warning onClick={onDelete}/>
      </div>
    </React.Fragment>
  )
}
