import React, { useContext, useEffect, useState } from 'react'
import { Trend, Search, Tweet } from 'Components'
import Context from 'Contexts/global'
import $ from 'jquery'
import places from 'Data/twitterPlaces'
import './style.scss'
const Home = (props) => {
	const state = useContext(Context)
	const [ trends, setTrends ] = useState(false)
	const [ selectedTrendIndex, setSelectedTrendIndex] = useState(false)
	const [ selectedPlaceIndex, setSelectedPlaceIndex] = useState(false)
	const [ searchTerm, setSearchTerm] = useState('')
	const [ tweets, setTweets ] = useState(false)

	useEffect(() => {
		
		return() => {

		}
	},[])


	function onTrendClick(index){
		setSelectedTrendIndex(index)
		$.get(`${state.host}/twittersearch?s=${trends[index].name.replace('#', '')}` , (res) => {
			console.log(res)
			setTweets(res)
		})
	}

	function onPlaceClick(index){
		console.log('setting ', index, places[index])
		setSelectedPlaceIndex(index)
		setSelectedTrendIndex(false)
		$.get(`${state.host}/twittertrends?s=${places[index].id}` , (res) => {
			setTrends(res)
		})
	}
	function onSubmitSearch(){
		$.get(`${state.host}/twittersearch?s=${searchTerm}` , (res) => {
			setSelectedPlaceIndex(false)
			setSelectedTrendIndex(false)
			setTweets(res)
		})
	}


	

	return (
		<div className="SocialContainer">
		<Search onSubmit={onSubmitSearch} placeholder="Search keyword" onChange={(e) => setSearchTerm(e.target.value)} value={searchTerm} />
			<div className="PlacesContainer">
				{places && places.map((trend, i) => {
					return(
						<Trend isSelected={i === selectedPlaceIndex} index={i} key={i} {...trend} onClick={onPlaceClick}/>
					)
				})}
			</div>
			<div className="TrendsContainer">
				{(selectedPlaceIndex || selectedPlaceIndex=== 0) && trends && trends.map((trend, i) => {
					return(
						<Trend isSelected={i === selectedTrendIndex}index={i} key={i} {...trend} onClick={onTrendClick}/>
					)
				})}
			</div>
			<div className="TweetsContainer">
				{tweets && tweets.map((trend, i) => {
					return(
						<Tweet item={trend} isSelected={i === selectedTrendIndex}index={i} key={i} {...trend} onClick={onTrendClick}/>
					)
				})}
			</div>
		</div>
	)
    
}

export default Home