import React, { useContext} from 'react'
import './style.scss'
import Context from 'Contexts/global'

const Draggable = ({type, isVoid, meta, item, children}) => {
	const state = useContext(Context)
	if (isVoid) {
		return <div>{children}</div>
	} else {

		return(
			<div className="DraggableTestContainer" draggable="true"
				onMouseDown={() => state.onDragMouseDown()}
				onMouseUp={() => state.onDragMouseUp()}
				onDragEnd={() => state.onDragEnd()}
				onDragStart={() => state.onDragStart({
					type: type,
					item: item,
					meta: meta,
				})}>
				{children}
			</div>

		)
	}

}

export default Draggable