import React, { useEffect, useState, useContext} from 'react'
import './style.scss'
import Context from 'Contexts/global'
import { Search, Member, Draggable } from 'Components'

import $ from 'jquery'
const News = ( ) => {
	const state = useContext(Context)
	const [ data, setData] = useState(false)
	const [ searchTerm, setSearchTerm] = useState('')
	useEffect(() => {
		
		
		return () => {

		}
	}, [])

	function onSubmitSearch(){
		$.get(`${state.host}/member?name=${searchTerm}` , (res) => {
			console.log('got members', res)
			setData(res.items)
		})
	}


	function getDate(date){
		let d = new Date(date)
		return d.toUTCString();
	}
	return(
		<div className={`membersContainer `}>
			<Search onSubmit={onSubmitSearch} placeholder="Search member name" onChange={(e) => setSearchTerm(e.target.value)} value={searchTerm} />
			<div className="membersInnerContainer">
			{data && data.map((item, i ) => {
				
				return(
					<Draggable {...{type: 'member', item: item.value.id }}>
					<Member {...item.value} key={i}/>
					</Draggable>
				)
			})}
		</div>
		
		</div>
	)
}

export default News