import React from 'react'
import './style.scss'

const Search = ({value, onChange, onSubmit, placeholder}) => {

	function onKeyDown(e){
		if (e.keyCode === 13){
			onSubmit()
		}
	}
	return(
		<div className="inputOuterContainer">
		<div className="inputContainer">
			<input type="text" value={value} onChange={onChange} placeholder={placeholder} onKeyDown={onKeyDown}/>
			<div className="submit" onClick={() => onSubmit()}>
				<img src={require('Assets/Icons/arrow.svg')} />
			</div>
		</div>
		</div>
	)
}

export default Search