import React from 'react'
import './style.scss'

const RowItem = ({children, onEdit, onClick}) => {
	return(
		<div className={`rowItem ${onClick && 'actionable'}`} onClick={onClick}>	
		{children}
		{onEdit &&
			<div className="editContainer" onClick={(e) => {
              e.stopPropagation()
              onEdit()
            }}>
              <img src={require('Assets/Icons/edit.svg')} />
            </div>
		}
		</div>
	)
}

export default RowItem