import React from 'react'
import './style.scss'

const SectionTitle = ({title, onAdd, secondary}) => {
	return(
		<div className={`sectionTitleContainer ${secondary && 'secondary'}`}>
			<p>{title}</p>
			{onAdd &&
				<div className="addButtonContainer" onClick={onAdd}>
					<img src={require('Assets/Icons/add.svg')} />
				</div>
			}
		</div>
	)
}

export default SectionTitle