import React from 'react'
import './style.scss'

const TextInput = ({onChange, value, placeholder, hideLabel, type, options, style}) => {
	return(
		<div>
		{!hideLabel &&
			<label>{placeholder}</label>
		}
		{(!type || type === 'textinput') &&
			<input className="textInput" placeholder={hideLabel ? placeholder: ''}onChange={onChange} value={value} style={style ? style : {}}/>
		}
		{type && type === 'textarea' &&
			<textarea className="textArea" onChange={onChange} value={value} style={style ? style : {}}/>	
		}
		{type && type === 'dropdown' &&
			<select>
				{options && options.map((item, i) => {
					return <option value={item.toLowerCase()}>{item}</option>
				})}
			</select>
		}
		{type && type === 'checkbox' &&
		
			<div className={`toggleOuter ${value && 'on'}`} onClick={onChange}>
				<div className="toggleInner" />
			</div>
			
		
		}
		</div>
	)
}

export default TextInput