import React, { useEffect, useState } from 'react'
import Edit from './edit'
import { navigate } from 'hookrouter'
import { EditModal , Button, RowItem, SectionTitle } from 'Components'
import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import './style.scss'
import { GET_PARTIES } from 'Data/queries'



export default function Departments() {
  const [ editing, setEditing ] = useState(false)
  const { loading, error, data, refetch } = useQuery(GET_PARTIES)

  function onCancelModal(){
    setEditing(false)
    refetch().then((data) => {
      
    })    
  }

  if (error) return <p>Error</p>
  if (loading) return <p>Loading</p>

  return (
    <div className="DepartmentsContainer">
      <SectionTitle title="Political parties" onAdd={() => setEditing(true)}/>
      {data && data.Party && data.Party.map((item, i) => {
        return(
          <RowItem key={i} onEdit={() => setEditing(item)} onClick={() => navigate(`party/${item.partyId}`)}>
            <div className="row">
            <div className="logo" style={item.logo ? {backgroundImage:`url(${item.logo})`} : {background:'#f2f2f2'}} />
            <div className="column">
            <p>{item.name}</p>
            <div className="partyColor" style={{backgroundColor:item.color}} />
            </div>
            
            </div>
          </RowItem>
        )
      })}
      {editing &&
        <EditModal >
          <Edit {...editing} onCancel={onCancelModal}/>
        </EditModal>
      }
    </div>
  )
}
