import React from 'react'
import './style.scss'

const Button = ({secondary, tirshary, label, warning, onClick, style}) => {
	return(
		<div
			style={style ? style : {}} 
			className={`ButtonContainer ${secondary && 'secondary'} ${tirshary && 'tirshary'} ${warning && 'warning'}`} 
			onClick={onClick}>
			<p>{label}</p>
		</div>
	)
}

export default Button