import React, { useState, useRef } from "react";
import { Provider } from "./Contexts/global";
import initialState from './initialState'
import Routes from './routes'
import { GridView, Menu, Article} from './Components'
import $ from 'jquery'
import './App.scss'
const host = process.env.NODE_ENV === 'development' ? 'http://localhost:4001/api' : '/api'

const articleTemplate = {
    image: '',
    title: '',
    policyAreas:[],
    issues:[],
    description: '',
    reactions: true,
    voteId: false,
    vote: false,
    items: []
  }

const ProviderComponent = props => {  
  let currentDroppable = useRef(null).current
  const [dragging, setDragging] = useState();
   const [loading, setLoading] = useState(false);
   const [showArticle, setShowArticle] = useState(false)
  const [article, setArticle] = useState(articleTemplate)
  const [isDroppable, setIsDroppable] = useState(false)
  

  function onMouseMove(event){
    let elemBelow = document.elementFromPoint(event.clientX, event.clientY);
    if (!elemBelow) return;
    let droppableBelow = elemBelow.closest('.droppable');
    if (currentDroppable != droppableBelow) {
      currentDroppable = droppableBelow
      if (currentDroppable) {
        setIsDroppable(true)
      }
    }
  }

  function addVoteToArticle(){
            const newArticle = Object.assign({}, article)
            let uri = 'https://www.google.com/search?q=hansard%20' + encodeURIComponent(dragging.item.Title.replace(/[^\w\s]/gi, ''))
            newArticle.title = dragging.item.Title
            newArticle.voteId = dragging.item.DivisionId
            newArticle.vote = dragging.item
            newArticle.type = 'vote'
            setArticle(newArticle)
            return

  }




  function onDragEnd(){
    const newArticle = Object.assign({}, article)
    if (isDroppable){
        setIsDroppable(false)

        switch(dragging.type){
          
          
          case 'vote':
            if (article.type){
              if (window.confirm("This will overwrite the current article.")) { 
                return addVoteToArticle()  
              }
            } else {
              return addVoteToArticle()
            }
            
          case 'image':
          
           
            newArticle.image = dragging.item.full
            setArticle(newArticle)
            return
          case 'member':
            return getMember(dragging.item, dragging.meta)
          default: 
            return updateArticle(dragging)
        }
    }
  }

  function resetArticle(){

    let newArticle = Object.assign({}, article)
    newArticle = articleTemplate
    setArticle(newArticle)
  }

  function getMember(id, meta){
    $.get(`${host}/memberbyid?id=${id}` , (res) => {
      let newMember = {
        ...res.value,
        ...meta
      }
      updateArticle({
        type: 'member',
        item: newMember
      })
    })
  }

  function updateArticleKeyValue(key, value){
      
      let newArticle = Object.assign({}, article)
        newArticle[key] = value
console.log(newArticle)
        setArticle(newArticle)
  }

  function updateArticle(item){
      let newItems = Object.assign([], article.items)
        newItems.push(item)
        article.items = newItems
        setArticle(article)
  }

  function onDragStart(item){
    setDragging(item)
    return false
  }

  function onDragMouseDown(){
    document.addEventListener('drag', onMouseMove);
  }

  function onDragMouseUp(){
    document.removeEventListener('drag', onMouseMove);
    
  }

  function makeQuery(funcToCall, args){
    console.log('funcToCall', funcToCall)

    return new Promise((resolve, reject) => {
      if (funcToCall){
       setLoading(true)
        funcToCall(args)
        .then(() => {
          
          resolve()
          setLoading(false)
        })
        .catch((err) => {
          reject()
        })
      } else {
        // return reject()
        return
      }
    })
    
  }

  return (
    <Provider
        value={{
          makeQuery,
          loading,
          setLoading,

          host: host,
          showArticle, 
          setShowArticle,
          onDragStart,
          updateArticleKeyValue,
          resetArticle,
          onDragEnd,
          currentDroppable,
          onDragMouseDown,
          onDragMouseUp,
          isDroppable,
          article
        }}
      >
     <Menu />     
       <div className="appContentContainer">
          <div className="routesContainer">
            <Routes />
          </div>
          <div className={`articleContainer ${showArticle && 'show'}`}>
            <Article />
          </div>
        </div>
        {loading && 
          <div className="loading" />
        }
    </Provider>
  );
};

export default ProviderComponent;