import React, { useState, useContext,} from 'react'
import './style.scss'
import Context from 'Contexts/global'
import Tweet from './Tweet'
import Vote from './Vote'
import Member from '../Member'
import Image from '../Image'
import ArticleHeader from './ArticleHeader'
import { TextInput, Button, Tagger } from 'Components'
import { useMutation } from '@apollo/react-hooks'
import { GET_POLICY_AREAS, GET_ISSUES, CREATE_ISSUE } from 'Data/queries'
// import { GET_POLICY_AREAS } from 'Data/queries'



const VoteArticle = ({state, createIssue}) => {
	const { type, title, image, items,description, voteId, vote, reactions, policyAreas, issues } = state.article
	return(
		<React.Fragment>
			<Vote {...vote} item={vote} />
			<TextInput onChange={(e) => state.updateArticleKeyValue('title', e.target.value)} value={title} placeholder="Title" />
			<TextInput onChange={(e) => state.updateArticleKeyValue('image', e.target.value)} value={image} placeholder="Image Url"/>
			<div className="articleImage2">
				<div style={{backgroundImage:`url(${image})`}} className="img"/>
			</div>
			<TextInput type="textarea"onChange={(e) => state.updateArticleKeyValue('description', e.target.value)} value={description} placeholder="Description"/>
			<Tagger title="Policy areas" GET_QUERY={GET_POLICY_AREAS} selected={policyAreas} setSelected={(value) => state.updateArticleKeyValue('policyAreas', value)}/>
			<Tagger title="Issues" createTag={createIssue} GET_QUERY={GET_ISSUES} selected={issues} setSelected={(value) => state.updateArticleKeyValue('issues', value)}/>
			{items &&
				<Items items={items} />
			}
		</React.Fragment>
	)
}
const GeneralArticle = ({state, createIssue}) => {
	const { type, title, image, items,description, voteId, vote, reactions, policyAreas, issues } = state.article
	
	return(
		<React.Fragment>
			<TextInput onChange={(e) => state.updateArticleKeyValue('title', e.target.value)} value={title} placeholder="Title" />
			<TextInput onChange={(e) => state.updateArticleKeyValue('image', e.target.value)} value={image} placeholder="Image Url"/>
			<div className="articleImage2">
				<p className="dragnDrop">Drag and drop image<br/>or paste URL above</p>
				<div style={{backgroundImage:`url(${image})`}} className="img"/>
			</div>
			<TextInput type="textarea"onChange={(e) => state.updateArticleKeyValue('description', e.target.value)} value={description} placeholder="Description"/>
			
			<Tagger title="Policy areas" GET_QUERY={GET_POLICY_AREAS} selected={policyAreas} setSelected={(value) => state.updateArticleKeyValue('policyAreas', value)}/>
			<Tagger title="Issues" createTag={createIssue} GET_QUERY={GET_ISSUES} selected={issues} setSelected={(value) => state.updateArticleKeyValue('issues', value)}/>
			<TextInput type="checkbox" onChange={() => state.updateArticleKeyValue('reactions', !reactions)} value={reactions} placeholder="Show final reactions"/>
			{items &&
				<Items items={items} />
			}
			
		</React.Fragment>
	)
}


const Items = ({items}) => {
	return(
		<div className={`newArticlePlaceholder`}>
			{items.map((item, i) => {
					switch(item.type){
						case'tweet':
							return <Tweet {...item.item} item={item.item} key={i} index={i} />
						case'vote':
							return <Vote {...item.item} item={item.item} key={i} index={i} />
						case'member':
							return <Member {...item.item} item={item.item} key={i} index={i} />
						case'image':
							return <Image {...item.item} url={item.item.regular} key={i} index={i} />
						default: return <p key={i}>{item}</p>
					}
				})
			}
		</div>
	)
}

const ButtonSet = ({onSave, onCancel, onDelete, articleId}) => {
	return(
		<div className="actionContainer">
			<Button style={{marginLeft:'4px'}}label="Save" onClick={() => {return}}/>
			<Button style={{marginLeft:'4px'}}label="Cancel" secondary onClick={() => onCancel()}/>
			{articleId &&
				<Button style={{marginLeft:'4px'}}label="Delete" warning onClick={() => {return}}/>
			}
		</div>
	)
}


const PolicyAreaList = ({}) => {
	return(
		<div>
		</div>
	)
}

const Article = () => {
	const state = useContext(Context)
	const [createIssue] = useMutation(CREATE_ISSUE);
	const [ isExpanded, setIsExpanded] = useState(false)
	const { type, title, image, description, voteId, vote, reactions, articleId } = state.article
	function onCancel(){
		state.resetArticle()
	}
	function onDelete(){

	}
	function onSave(){

	}
	if (!type){
  		return(
  			<div className={`ArticleContainer ${isExpanded && 'isExpanded'} droppable ${state.isDroppable && 'isActiveDroppable'}`} onClick={() => setIsExpanded(!isExpanded)} onDragOver={(e) => e.preventDefault()}>	
				<div className="ArticleInnerContainer">
					<p>Drag and drop vote here or</p>
					<Button label="Create new article" onClick={() => state.updateArticleKeyValue('type', 'general')}/>
				</div>
			</div>
  		)
  	}
	
  	
  	if (type === 'vote'){
  		return(
  			<div className={`ArticleContainer ${isExpanded && 'isExpanded'} droppable ${state.isDroppable && 'isActiveDroppable'}`} onClick={() => setIsExpanded(!isExpanded)} onDragOver={(e) => e.preventDefault()}>	
				<div className="ArticleInnerContainer">
					<VoteArticle state={state} createIssue={createIssue}/>
					<ButtonSet articleId={articleId} onSave={onSave} onCancel={onCancel} onDelete={onDelete} />
				</div>
			</div>
  		)
  	} else {
  		return(
	  		<div className={`ArticleContainer ${isExpanded && 'isExpanded'} droppable ${state.isDroppable && 'isActiveDroppable'}`} onClick={() => setIsExpanded(!isExpanded)} onDragOver={(e) => e.preventDefault()}>	
				<div className="ArticleInnerContainer">
						<GeneralArticle state={state} createIssue={createIssue}/>
						<ButtonSet articleId={articleId} onSave={onSave} onCancel={onCancel} onDelete={onDelete} />
				</div>
			</div>
  		)
  	}
}

export default Article 