import React, { useContext} from 'react'
import './style.scss'
import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import { RowItem, SectionTitle } from 'Components'
import { GET_DEPARTMENTS, ADD_DEPARTMENT} from 'Data/queries'
import Context from 'Contexts/global'






const ChooseItem = ({title, personId, onComplete}) => {
	const state = useContext(Context)
	const { loading, error, data, refetch } = useQuery(GET_DEPARTMENTS)
	const [addDepartment] = useMutation(ADD_DEPARTMENT);
	
	function onChooseItem(item){
		state.makeQuery(addDepartment, { variables: {departmentId: item.departmentId, personId: personId} }).then(() => {
			
			onComplete()
		})
	}

	if (error) return <p>Error</p>
  	if (loading) return <p>Loading</p>
	
	return(
		<div className="chooseItemContainer">
			<SectionTitle title={"Add department"} secondary/>
			{data && data.Department && data.Department.map((item, i) => {
	        return(
	        	<RowItem>
	          <div onClick={() => onChooseItem(item)}>
	            <p>{item.name}</p>
	           </div>
	           </RowItem>
	        )
	      })}
		</div>
	)
}

export default ChooseItem