import React, { useContext} from 'react'
import './style.scss'
import Draggable from '../Draggable'
const Tweet = ({text, created_at, user, item}) => {
	const { profile_image_url_https, screen_name, name } = user
	return(
		<div className="tweetOuterContainer">
		<Draggable {...{type: 'tweet', item: item }}>
			 <div className="TweetContainer">
				<div className="userContainer">
					<img src={profile_image_url_https} />
					<div className="userMetaContainer">
						<p className="userName">{name}</p>
						<p className="userScreenName">@{screen_name}</p>

					</div>
				</div>
				<p>{text}</p>
			</div>
		</Draggable>
		</div>
	)

}

export default Tweet