import React, { useEffect, useState } from 'react'

import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import govDepartments from 'Data/govDepartments'
import { TextInput, Button } from 'Components'

const ADD_PARTY  = gql`
  mutation CreateParty($name: String!, $color:String!, $logo:String, $partyId:ID!) {
    CreateParty(name: $name, color:$color, logo: $logo, partyId: $partyId) {
      name
      color
      logo
      partyId
    }
  }
`;

const UPDATE_PARTY  = gql`
  mutation UpdateParty($partyId: ID!, $name: String!,$color:String!, $logo:String!) {
    UpdateParty(partyId: $partyId, name: $name, color:$color, logo: $logo) {
      partyId
      name
      color
      logo
    }
  }
`;
const DELETE_PARTY  = gql`
  mutation DeleteParty($partyId: ID!) {
    DeleteParty(partyId: $partyId) {
      partyId
    }
  }
`;

export default function CreateDepartment(props) {
  const [addParty] = useMutation(ADD_PARTY);
  const [updateParty] = useMutation(UPDATE_PARTY);
  const [deleteParty] = useMutation(DELETE_PARTY);
  const [name, setName] = useState(props.name || '')
  const [color, setColor] = useState(props.color || '')
  const [logo, setLogo] = useState(props.logo || '')
  const [id, setId] = useState(props.partyId || '')

  function submit(){
    if (!props.partyId){
      addParty({ variables: { name: name, color: color, logo: logo, partyId: id} })
      .then(() => {
        props.onCancel()
      })
      .catch((err) => props.onCancel());
      
    } else {
      console.log('updating party')
      updateParty({ variables: {partyId: props.partyId, name: name, color: color, logo: logo, partyId: id} })
      .then(() => {
        props.onCancel()
      })
      .catch((err) => props.onCancel());
      
    }
  }

  function onDelete(){
    deleteParty({ variables: {partyId: props.partyId} })
    .then(() => {
      props.onCancel()
    })
    .catch((err) => props.onCancel());
  }

  return (
    <React.Fragment>    
      <TextInput onChange={(e) => setName(e.target.value)} value={name} placeholder="Name" />
      <TextInput onChange={(e) => setColor(e.target.value)} value={color} placeholder="Color" />
      <TextInput onChange={(e) => setLogo(e.target.value)} value={logo} placeholder="Logo" />
      <TextInput onChange={(e) => setId(e.target.value)} value={id} placeholder="Logo" />
      <div className="actionContainer">
          <Button style={{marginLeft:'4px'}}label="Save" onClick={submit}/>
          <Button style={{marginLeft:'4px'}}label="Cancel" secondary onClick={props.onCancel}/>
          <Button style={{marginLeft:'4px'}}label="Delete" warning onClick={onDelete}/>
      </div>
    </React.Fragment>
  )
}
