import React, { useContext, useEffect, useState } from 'react'
import { Vote, Search , Image, Draggable} from 'Components'

import Context from 'Contexts/global'
import $ from 'jquery'
import './style.scss'

const Images = (props) => {
	const state = useContext(Context)
	const [ images, setImages ] = useState(false)
	const [ searchTerm, setSearchTerm] = useState('')

	useEffect(() => {
		
		$.get(`${state.host}/photos` , (res) => {
			// console.log(res)
			
			setImages(JSON.parse(res))
		})
		// $.get(`${state.host}/newssources` , (res) => {
		// 	console.log(res)
		// })
		return() => {

		}
	},[])

	function onSubmitSearch(){
		$.get(`${state.host}/photos?s=${searchTerm}` , (res) => {
			// console.log(res)
			console.log('res',JSON.parse(res))
			setImages(JSON.parse(res).results)
		})
	}
	return (
		<div className="ImagesContainer">
			<Search onSubmit={onSubmitSearch} placeholder="Search keyword" onChange={(e) => setSearchTerm(e.target.value)} value={searchTerm} />
			{images && images.map((item, i) => {
				return(
					<div className="imageOuterContainerHalf">
					<Draggable {...{type: 'image', item: item.urls}}>
					<Image url={item.urls.regular} key={i}/>
					</Draggable>
					</div>
				)
			})}
			
		</div>
	)
    
}

export default Images