import React, { useEffect, useState, useContext } from 'react'
import $ from 'jquery';
import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import govDepartments from 'Data/govDepartments'
import { TextInput, Button } from 'Components'
import Context from 'Contexts/global'
import EditRelationships from './editRelationships'
import { ADD_PERSON,UPDATE_PERSON, DELETE_PERSON, ADD_PERSON_PARTY } from 'Data/queries'
import './style.scss'
let timeout;


export default function CreateDepartment(props) {
  const state = useContext(Context)
  const [people, setPeople ] = useState([])

  const [addPerson] = useMutation(ADD_PERSON);
  const [updatePerson] = useMutation(UPDATE_PERSON);
  const [deletePerson] = useMutation(DELETE_PERSON);
  const [addPersonParty] = useMutation(ADD_PERSON_PARTY);

  const [name, setName] = useState(props.name || '')
  const [image, setImage] = useState(props.image || '')
  const [twitter, setTwitter] = useState(props.twitter || '')
  const [jobTitle, setJobTitle] = useState(props.jobTitle || '')
  const [party, setParty] = useState(props.party || '')
  const [id, setId] = useState(props.personId || '')

  



  function submit(addRelationships){
    function onSubmitComplete(){
      if (addRelationships){   
          props.onAddRelationships({personId:id})
        } else {
          props.onCancel()
        }
    }  
    if (!props.personId){
      state.makeQuery(addPerson,{ variables: { name, id, jobTitle, twitter, image}})
      .then(() => {
        if (party || party === 0){
          state.makeQuery(addPersonParty, { variables: {partyId: party, personId: id} }).then(() => {
            onSubmitComplete()
          

          })
        } else {
          onSubmitComplete()
        }
        
      })
      .catch((err) => props.onCancel());
      
    } else {
      
      updatePerson({ variables: {name, party, id, jobTitle, twitter, image } })
      .then(() => {
        props.onCancel()
      })
      .catch((err) => props.onCancel());
      
    }
  }

  function onDelete(){
    deletePerson({ variables: {personId: props.personId} })
    .then(() => {
      props.onCancel()
    })
    .catch((err) => props.onCancel());
  }

  function onSearchPerson(value){
    window.clearTimeout(timeout)
    timeout = setTimeout(()=> {
      $.get(`${state.host}/member?name=${value}` , (res) => {
        
        setPeople(res.items)
      })
    },500)
    
  }

  function populatePerson(person){
    setName(person.value.nameDisplayAs)
    setImage(person.value.thumbnailUrl)
    setId(person.value.id)
    setParty(person.value.latestParty.id)

  }

  function onAddRelationships(){

  }

  return (
    <React.Fragment>  
      {props.isAdd &&
      <div className="searchPeople">  
        <TextInput onChange={(e) => onSearchPerson(e.target.value)} placeholder="Search member" />
        <div className="searchPeopleContainer">
        {people && people.map((item, i) => {
          
          return(
            <div className="searchPersonContainer" onClick={() => populatePerson(item)}>
            <div key={i} className="searchPersonImageContainer" style={{backgroundImage:`url(${item.value.thumbnailUrl})`}}>
            </div>
            <p>{item.value.nameDisplayAs}</p>
            </div>
          )
        })}
        </div>

      
      </div>  
    }
      <TextInput onChange={(e) => setName(e.target.value)} value={name} placeholder="Name" />
      <TextInput onChange={(e) => setParty(e.target.value)} value={party} placeholder="Party" />
      <TextInput onChange={(e) => setTwitter(e.target.value)} value={twitter} placeholder="Twitter" />
      <TextInput onChange={(e) => setJobTitle(e.target.value)} value={jobTitle} placeholder="Job (If not politician)" />
      <TextInput onChange={(e) => setImage(e.target.value)} value={image} placeholder="Image" />
      <TextInput onChange={(e) => setId(e.target.value)} value={id} placeholder="Id" />
      {props.isAdd &&
        <Button style={{marginLeft:'4px'}}label="Add relationships" onClick={() => submit(true)}/>
      }
      <div className="actionContainer">
          <Button style={{marginLeft:'4px'}}label="Save" onClick={() => submit(false)}/>
          <Button style={{marginLeft:'4px'}}label="Cancel" secondary onClick={props.onCancel}/>
          <Button style={{marginLeft:'4px'}}label="Delete" warning onClick={onDelete}/>
      </div>
      {!props.isAdd && 
        <EditRelationships id={id}/>
      }

    </React.Fragment>
  )
}
