import React, { useContext } from 'react'
import './style.scss'
import { useQuery, useMutation } from '@apollo/react-hooks'
import { RowItem } from 'Components'
import { GET_PARTIES, ADD_PERSON_PARTY } from 'Data/queries'
import Context from 'Contexts/global'

const ChooseParty = ({title, personId, onComplete}) => {
	const state = useContext(Context)
	const { loading, error, data, refetch } = useQuery(GET_PARTIES)
	const [addPersonParty] = useMutation(ADD_PERSON_PARTY);

	function onChooseItem(item){
		state.makeQuery(addPersonParty, { variables: {partyId: item.partyId, personId: personId} }).then((callback) => {
			
			onComplete()

		})
	}

	if (error) return <p>Error</p>
  	if (loading) return <p>Loading</p>
	
	return(
		<div className="chooseItemContainer">
			{data && data.Party && data.Party.map((item, i) => {
	        return(
	        	<RowItem key={i}>
	          <div onClick={() => onChooseItem(item)}>
	            <p>{item.name}</p>
	           </div>
	           </RowItem>
	        )
	      })}
		</div>
	)
}

export default ChooseParty