import React, { useContext, useEffect, useState } from 'react'
import Context from 'Contexts/global'
import $ from 'jquery'

const Posts = (props) => {
	const state = useContext(Context)
	const [ postcode, setPostcode ] = useState('e149xb')

	function standardise(str){
		str = str.replace(/\s/g, '');
		return str.toUpperCase()
	}

	function onSubmit(){
		$.get(`${state.host}/postcode?postcode=${postcode}`, (res) => {
			
			console.log(res)
		})
	}

	return (
		<div className="PostsContainer">
			<input value={postcode} onChange={(e) => setPostcode(standardise(e.target.value))} />
			<div onClick={onSubmit}>
			Submit
			</div>
		</div>
	)
    
}

export default Posts